.card-search-container {
  width: 100%;
  overflow: hidden;
  padding: 20px;
}

.editor-sidebar-bracket {
  padding-right: 280px;
}

.editor-sidebar-bracket-closed {
  padding-right: 20px;
}

.editor-container {
  display: flex;
  width: 100%;
}

.view-tabs {
  padding-top: 20px;
  border-top: 1px solid purple;
  display: flex;
  margin-bottom: 20px;
}

.view-tab {
  text-align: center;
  padding-top: 10px;
  width: 160px;
  height: 50px;
  cursor: pointer;
}

.view-tab:hover .line,
.line:hover {
  background-color: purple;
}

@media screen and (max-width: 800px) {
  .display-mobile-deck-card {
    display: flex;
    justify-content: center;
  }

  .searched-cards,
  .reccommended-cards {
    justify-content: center;
  }
}
